html {
	font-size: 62.5%;
}

body {
	font-family: $primaryFont;
	color: white;
}

.poweredBy h3 {
	font-size: clamp(1rem, 0.7742rem + 0.6452vw, 1.6rem);
}

header h1 {
	font-size: clamp(3rem, 2.2473rem + 2.1505vw, 5rem);}

h2,
.symbol,
.longName,
.regularMarketPrice,
.regularMarketPriceHeader,
.closePriceHeader {
	font-size: clamp(2.2rem, 1.8939rem + 1.0204vw, 3.2rem);
}

.marketStatus h2 {
	font-size: clamp(1.8rem, 1.7247rem + 0.2151vw, 2rem);
}

h3,
input {
	font-size: 1.6rem;
}

.movers {
	font-size: clamp(1.2rem, 1.0495rem + 0.4301vw, 1.6rem);
}

span,
.errorHandling {
	font-size: 2.4rem;
}

.symbol,
.longName {
	font-weight: bold;
}

.quotesContent h2 {
	font-size: 2rem;
	font-style: italic;
}

label,
.quotesContent h3,
.daysChangeDollars,
.daysChangePercent,
.previousClosingPrice,
.openingPrice,
.regularMarketPrice,
.daysRange,
.fiftyTwoWeekRange,
.regularMarketVolume,
.averageVolume10Day,
.closePrice {
	font-size: clamp(1.6rem, 1.4163rem + 0.6122vw, 2.2rem);
}

.symbol,
.longName,
.previousClosingPrice,
.openingPrice,
.regularMarketPrice,
.daysRange,
.fiftyTwoWeekRange,
.regularMarketVolume,
.averageVolume10Day,
.closePrice {
	color: black;
}

button {
	color: white;
	font-family: $primaryFont;
}

.profileInformation button,
footer a {
	font-size: 1.4rem;
}

footer a {
	text-decoration: none;
	color: white;
}
